import React, {useReducer} from 'react'
import AlertContext from './alertContext'
import alertReducer from './alertReducer'
import {v4 as uuidv4} from 'uuid'

import {
  REGISTER_SUCCESS,
  REGISTER_FAIL,
  USER_LOADED,
  AUTH_ERROR,
  LOGIN_SUCCESS,
  LOGIN_FAIL,
  LOGOUT,
  CLEAR_ERRORS,
  SET_ALERT,
  REMOVE_ALERT
} from '../types'




const AlertState = props => {
  const initialState = []
  
  const [state, dispatch] = useReducer(alertReducer, initialState)

// Set Alert
const setAlert = (msg, type, timeout= 5000) => {
const id = uuidv4()
dispatch({
  type: SET_ALERT,
  payload: {msg, type, id}
})

setTimeout(() => dispatch({
  type: REMOVE_ALERT,
  payload: id
}), timeout)
} 

  return (
    <AlertContext.Provider 
    value = {{
   alerts: state,
   setAlert
    }}
    >
    {props.children}
    </AlertContext.Provider>
  )
} 

export default AlertState 